"use client"
import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Minus, Plus } from "lucide-react";

const NumberInput = ({ min = 1, max = 20, step = 1 }) => {
  const [value, setValue] = useState(min);

  const increment = () => {
    if (value < max) setValue(value + step);
  };

  const decrement = () => {
    if (value > min) setValue(value - step);
  };

  return (
    <div className="flex items-center space-x-2">
      <Button variant="outline" size="icon" onClick={decrement} disabled={value <= min}>
        <Minus className="w-4 h-4" />
      </Button>
      <Input
        type="number"
        className="w-16 text-center"
        value={value}
        onChange={(e) => setValue(Number(e.target.value))}
      />
      <Button variant="outline" size="icon" onClick={increment} disabled={value >= max}>
        <Plus className="w-4 h-4" />
      </Button>
    </div>
  );
};

export default NumberInput;
